import styled from 'styled-components';
import { devices } from '../styles/device';

const MoneyValue = ({ money, description }) => {
	const handleFormatterPrice = new Intl.NumberFormat('en-US', {
		style: 'currency',
		currency: 'USD',
	});

	return (
		<MoneyValueWrapper>
			<NamesOfMoney>{description}</NamesOfMoney>
			<YouMoney>
				<Money>{handleFormatterPrice.format(money)}</Money>
			</YouMoney>
		</MoneyValueWrapper>
	);
};

const MoneyValueWrapper = styled.div`
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 90%;
	height: 40px;

	background: linear-gradient(90deg, #828282 0.63%, rgba(48, 0, 49, 0.44) 100%);
	box-shadow: 0px 1px 2px #000000, inset 0px 0px 4px #000000;
	border-radius: 11px;

	@media ${devices.tablet} {
		height: 50px;
		margin-bottom: 15px;
		margin-top: 15px;
	}

	@media ${devices.laptop} {
		height: 60px;

		margin-top: 15px;
		margin-bottom: 15px;
	}
`;

const NamesOfMoney = styled.p`
	font-size: 16px;
	font-weight: bold;
	text-align: center;
	margin-left: 10px;

	@media ${devices.tablet} {
		font-size: 20px;
	}

	@media ${devices.laptop} {
		font-size: 24px;
	}
`;

const YouMoney = styled.div`
	width: 130px;
	height: 30px;
	margin-right: 5px;

	background: rgba(217, 217, 217, 0.17);
	border-radius: 54px;

	@media ${devices.tablet} {
		width: 150px;
		height: 35px;
	}
`;

const Money = styled.p`
	font-size: 16px;
	font-weight: bold;
	color: #fff;
	text-align: center;
	line-height: 30px;

	@media ${devices.tablet} {
		font-size: 20px;
		line-height: 35px;
	}

	@media ${devices.laptop} {
		font-size: 24px;
	}
`;

export default MoneyValue;
