import styled from 'styled-components';

import { devices } from '../styles/device';

const TopMenue = () => {
	return (
		<TopMenueWrapper>
			<AppName>Crypto King</AppName>
		</TopMenueWrapper>
	);
};

const TopMenueWrapper = styled.div`
	text-align: center;
	line-height: 60px;
	width: 100%;
	max-width: 768px;
	height: 60px;

	background: linear-gradient(
		180deg,
		rgba(0, 0, 0, 0.16) 0%,
		rgba(0, 0, 0, 0.67) 100%
	);
	border-radius: 0px 0px 20px 20px;

	@media ${devices.laptop} {
		position: fixed;
		left: 0;
		top: 0;
		text-align: left;
		max-width: none;
		width: 100%;
		border-radius: 0;

		background: linear-gradient(180deg, rgb(107 20 117) 0%, rgb(60 0 57) 100%);
	}
`;

const AppName = styled.h1`
	color: #ffffff;
	font-size: 28px;
	font-weight: bold;

	margin-left: 20px;
`;

export default TopMenue;
