import styled from 'styled-components';

import chartsNochange from '../assets/chartsGrey.png';
import chartsMinus from '../assets/chartsRed.png';
import chartsPlus from '../assets/chartsGreen.png';

import { devices } from '../styles/device';

const LitteCoin = props => {
	const { symbol, change, price, marketCap, iconUrl } = props.coinData;
	let color = false;
	let charts = chartsNochange;
	let formaterChange = [...change];

	// console.log(change);
	if (change === '-0.00') {
		formaterChange = formaterChange.slice(1, 5);
	}

	if (Number(change) < 0) {
		color = '#FF0000';
		charts = chartsMinus;
	} else if (Number(change) > 0) {
		color = '#33FF00';
		charts = chartsPlus;
	}

	const handleFormatterPrice = new Intl.NumberFormat('en-US', {
		style: 'currency',
		currency: 'USD',
	});
	return (
		<LitteCoinWrapper>
			<CoinIcon src={iconUrl} alt='Coin Icon' />
			<CoinShortName>{symbol}</CoinShortName>
			<CoinPriceChange $color={color}>{formaterChange}%</CoinPriceChange>

			<CoinCharts src={charts} alt='Charts' />
			<CoinPrice>{handleFormatterPrice.format(price)}</CoinPrice>
			<CoinMarketCap>{handleFormatterPrice.format(marketCap)}</CoinMarketCap>
		</LitteCoinWrapper>
	);
};

const LitteCoinWrapper = styled.div`
	display: grid;
	grid-template-columns: 65px repeat(6, 1fr);
	grid-template-rows: repeat(2, 1fr);
	align-items: center;
	justify-items: center;

	min-width: 320px;
	min-height: 60px;
	width: 90vw;
	margin-bottom: 27px;
	cursor: pointer;

	border-radius: 15px;
	background: linear-gradient(
		90deg,
		rgba(131, 94, 0, 0.2) 0%,
		rgba(0, 0, 0, 0.76) 54.69%,
		#5e4040 100%
	);
	box-shadow: inset 1px -2px 10px -1px #000000;

	@media ${devices.mobileL} {
		height: 80px;
	}
	@media ${devices.tablet} {
		max-width: 320px;
		margin-right: 20px;
	}

	@media ${devices.laptop} {
		justify-content: center;
		grid-template-columns: repeat(3, 1fr);
		grid-template-rows: repeat(6, 1fr);
		height: 280px;
		width: 200px;
	}
`;

const CoinIcon = styled.img`
	grid-column: 0/1;
	grid-row: 1/3;
	width: 45px;
	height: 45px;
	border-radius: 11px;

	@media ${devices.mobileL} {
		height: 55px;
		width: 55px;
	}
	@media ${devices.tablet} {
		height: 45px;
		width: 45px;
	}

	@media ${devices.laptop} {
		grid-column: 1/3;
		grid-row: 1/4;

		height: 90px;
		width: 90px;
	}
`;

const CoinShortName = styled.h3`
	grid-column: 2/3;
	grid-row: 1/2;
	font-size: 18px;
	margin-top: 10px;

	color: #ffffff;
	font-weight: bold;

	@media ${devices.mobileL} {
		font-size: 20px;
	}

	@media ${devices.tablet} {
		font-size: 18px;
	}

	@media ${devices.laptop} {
		grid-column: 3/4;
		grid-row: 2/3;
		font-size: 26px;
	}
`;

const CoinPriceChange = styled.p`
	grid-column: 2/3;
	grid-row: 2/3;
	font-size: 12px;
	padding-bottom: 10px;

	color: ${props => props.$color || '#818181'};

	@media ${devices.mobileL} {
		font-size: 14px;
	}
	@media ${devices.tablet} {
		font-size: 12px;
	}

	@media ${devices.laptop} {
		grid-column: 3/4;
		grid-row: 4/5;

		font-size: 24px;
		padding-bottom: 0;
	}
`;

const CoinCharts = styled.img`
	grid-column: 3/6;
	grid-row: 1/3;
	height: 42px;
	width: 100px;

	@media ${devices.mobileL} {
		width: 120px;
		height: 50px;
	}

	@media ${devices.tablet} {
		width: 100px;
		height: 42px;
	}

	@media ${devices.laptop} {
		grid-column: 1/4;
		grid-row: 5/8;

		width: 180px;
		height: 70px;
		margin-bottom: 10px;
		margin-top: 10px;
	}
`;

const CoinPrice = styled.p`
	grid-column: 6/8;
	grid-row: 1/2;
	font-size: 14px;
	margin-top: 10px;

	color: #ffffff;

	@media ${devices.mobileL} {
		font-size: 18px;
	}

	@media ${devices.tablet} {
		font-size: 14px;
	}

	@media ${devices.laptop} {
		grid-column: 1/3;
		grid-row: 4/5;

		font-size: 24px;
		margin-top: 0;
	}
`;

const CoinMarketCap = styled.p`
	grid-column: 6/8;
	grid-row: 2/3;
	font-size: 8px;
	margin-bottom: 10px;

	color: #c2c2c2;

	@media ${devices.mobileL} {
		font-size: 10px;
	}

	@media ${devices.tablet} {
		font-size: 8px;
	}

	@media ${devices.laptop} {
		grid-column: 2/4;
		grid-row: 3/4;

		font-size: 14px;
		margin-bottom: 0;
		margin-left: 60px;
	}
`;

export default LitteCoin;
