import { Link } from 'react-router-dom';
import styled from 'styled-components';

import { devices } from '../styles/device';

import arrow from '../assets/Arrow .png';

const CoinHeaders = ({ title, rightIcon, setFavoriteCoin }) => {
	const rightIconComponent = rightIcon ? (
		<RightIconBtn onClick={setFavoriteCoin}>
			<img src={rightIcon} alt='Icon' />
		</RightIconBtn>
	) : null;

	return (
		<CoinHeadersWrapper>
			<LinkElem to='/'>
				<img src={arrow} alt='Go to home page' />
			</LinkElem>
			<PagesTitle rightIcon={rightIcon}>{title}</PagesTitle>

			{rightIconComponent}
		</CoinHeadersWrapper>
	);
};

const CoinHeadersWrapper = styled.div`
	display: flex;
	justify-content: space-between;
	align-items: center;
	min-width: 320px;
	max-width: 768px;
	width: 90%;
	height: 40px;
	margin-top: 12px;

	background: linear-gradient(90deg, #bebebe 0.63%, #300031 100%);
	box-shadow: 0px 1px 2px #000000, inset 0px 0px 4px #000000;
	border-radius: 11px;

	@media ${devices.mobileL} {
		height: 50px;
	}

	@media ${devices.tablet} {
		height: 60px;
	}

	@media ${devices.laptop} {
		margin-top: 80px;
	}
`;

const LinkElem = styled(Link)`
	margin-left: 10px;
`;

const RightIconBtn = styled.button`
	all: unset;

	margin-right: 10px;
	cursor: pointer;
`;

const PagesTitle = styled.h3`
	font-size: 24px;
	color: #fff;
	font-weight: bold;

	margin-right: ${props => (props.rightIcon ? '0' : '45%')};

	@media ${devices.tablet} {
		font-size: 28px;
	}
`;

export default CoinHeaders;
